.dashboard{
    padding-block: 24px;
    margin-left: 120px;
}

.dashboard-wrapper{
    display: flex;
}

.dashboard-outer{
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;
    align-items: flex-start;
    margin-left: 2rem;
}

.dashboard-outer .diagram {
    width: 479px;
    height: 333px;
}

.dashboard .header-inner{
    padding-bottom: 24px;
    border-bottom: 1px solid var(--clr-dark-300);
}

.diagram-img {
    margin-top: 16px !important;
}